      /**/
      dateFormat(row, col) {
        let resDate
        let date = row[col.property]
        if (date != null) {
          resDate = this.formatTime(date)
        }
        return resDate
      },
      formatTime(time) { //
        return formatTime(time, "yyyy-MM-dd hh:mm:ss")
      },
      /* value*/
      timeChange(value) {
        let articleNum = ''
        let type = ''
        if (type !== " ") {
          type = this.type
        }
        if (this.articleNum !== " ") {
          articleNum = this.articleNum
        }
        let startTime = ''
        let endTime = ''
        if (this.times != null) {
          startTime = value[0]
          endTime = value[1]
        }
        this.requestInterface(articleNum, startTime, endTime, type, this.page)
      },
      /* values*/
      inputChange(value) {
        let type = ''
        let startTime = ''
        let endTime = ''
        if (type !== " ") {
          type = this.type
        }
        if (this.times != null) {
          if (this.times.length != 0) {
            startTime = this.times[0]
            endTime = this.times[1]
          }
        }
        this.requestInterface(value, startTime, endTime, type, this.page)
      },
      /*value*/
      radioChange(value) {
        // 
        let articleNum = ''
        let startTime = ''
        let endTime = ''
        if (this.articleNum !== " ") {
          articleNum = this.articleNum
        }
        if (this.times != null) {
          if (this.times.length != 0) {
            startTime = this.times[0]
            endTime = this.times[1]
          }
        }
        this.requestInterface(articleNum, startTime, endTime, value, this.page)
      },
      requestInterface(articleNum, startTime, endTime, type, currPage) {
        axiosUtil({
          url: '/getAllArticle',
          method: 'get',
          params: {
            startTime: startTime,
            endTime: endTime,
            type: type,
            articleNum: articleNum,
            currPage: currPage
          }
        }).then(res => {
          if (res.code === 200) {
            this.tableData = res.data
            this.total = res.data.length
          }
        })
      },
      /* */
      defaultF() {
        this.articleNum = ''
        this.times = ''
        this.type = ''
        this.requestInterface("", "", "", "", this.page)
      },
      //
      currPage(page) {
        this.page = page
        this.requestInterface(this.articleNum, this.times[0], this.times[1], this.type, this.page)
      },
      /*
      * excel
      * */
      beforeDownL() {
        this.showProgress = true
        for (let i = 0; i < 50; i++) {
          this.progress += 2
        }
      },
      /*
      * excel
      * */
      beforeFin() {
        setTimeout(() => {
